<template>
    <div class="table">
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="销售单号" :inline="true">
                    <el-input v-model="filters.sale_num_code" placeholder="请输入销售单号"></el-input>
                </el-form-item>
                <el-form-item label="客户名称" :inline="true">
                    <el-input v-model="filters.cus_user_name" placeholder="请输入客户名称"></el-input>
                </el-form-item>
                <!-- <el-form-item label="订单阶段">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="订单状态">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-button>查询</el-button>
                <el-button type="primary" icon="plus" @click="handleSelectClient('default')">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" width="55" align="center"></el-table-column>
                <el-table-column prop="sale_num_code" label="销售单号" width="150">
                </el-table-column>
                <el-table-column prop="total_car_num" label="车辆数" width="120">
                </el-table-column>
                <el-table-column prop="total_car_price" label="金额" width="120">
                </el-table-column>
                <el-table-column prop="cus_user_name" label="客户名称" width="120">
                </el-table-column>
                <el-table-column prop="stage_name" label="订单阶段" width="120">
                </el-table-column>
                <el-table-column prop="work_status_name" label="订单状态" width="120">
                </el-table-column>
                <el-table-column prop="user_name" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建日期">
                    <template slot-scope="{row}">
                        {{row.create_time | time}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button
                            type="text"
                            icon="el-icon-view"
                            @click="$router.push({path: `/${link(row)}/view`, query: { id: row.id, stage_id: row.stage_id}})">
                            查看
                        </el-button>
                        <el-button
                            v-if="row.stage_id==1&&row.work_status==1&&row.deal_user==1"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: `/${link(row)}/edit`, query: { id: row.id, stage_id: row.stage_id}})">
                            修改
                        </el-button>
                        <el-button
                            v-if="isShowDispose(row)"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: `/${link(row)}/edit`, query: { id: row.id, stage_id: row.stage_id}})">
                            处理
                        </el-button>
                        <el-button
                            v-if="isShowRefund(row)"
                            type="text"
                            icon="el-icon-view"
                            @click="handleRefund(row, $index)">
                            申请退款
                        </el-button>
                        <el-button
                            v-if="isShowAudit(row)"
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: `/${link(row)}/edit`, query: { id: row.id, stage_id: row.stage_id}})">
                            审核
                        </el-button>
                        <!-- <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/refundManagement/edit', query: { id: row.id}})">
                            退款
                        </el-button> -->
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
                </el-pagination>
            </div>
        </div>
        
        <!-- 选择客户 -->
        <el-dialog title="选择客户" :visible.sync="dialogSelectClient" width="50%">
            <selectClient v-if="hackResetClient" :selectClientOptions="selectClientOptions" @emitClientSubmit="emitClientSubmit" @emitClientClose="emitClientClose"></selectClient>
        </el-dialog>

        <!-- 申请退款 -->
        <el-dialog title="申请退款" :visible.sync="dialogRefund" width="50%">
            <el-form ref="formRefund" :model="formRefund" label-width="100px">
                <el-form-item label="退款原因">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" v-model="formRefund.why_refund"></el-input>
                </el-form-item>
                <el-form-item label="补充资料">
                    <el-upload
                        :action="IMG_UPLOAD_URL"
                        :on-success="handleUploadSuccess_refund_img"
                        :file-list="formRefund.factors_img"
                        list-type="picture-card">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="选择审核人" class="mt15">
                    <el-select v-model="company" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                        <el-option
                            v-for="item in optionsCompany"
                            :key="item.id"
                            :label="item.mc_name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                    <el-select v-model="formRefund.first_exec_id" placeholder="请选择审核人">
                        <el-option
                            v-for="item in optionsExecUser"
                            :key="item.id"
                            :label="item.user_name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogRefund = false">取 消</el-button>
                <el-button type="primary" @click="dialogRefundSubmit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/salesManagement'
    import { requestUrl } from '@/api/config'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import selectClient from '@/components/selectClient'
    export default {
        data() {
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                multipleSelection: [],
                loading: false,
                company: '',
                hackResetClient: false,
                dialogSelectClient: false,
                selectClientOptions:{
                    formConfig: {
                        val: false
                    },
                    pageType: 'default',
                    isNextButton: true
                    
                },
                dialogRefund: false,
                delVisible: false,
                formRefund: {
                    sale_id:'', //销售ID
                    why_refund:'', //退款原因
                    factors_img:[], //图片
                    first_exec_id:'', //审核人
                },
                filters: {
                    value:'3',
                    sale_num_code: '',
                    cus_user_name: '',
                },
                optionsExecUser:[],
                optionsCompany:[],
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        components:{
            selectClient
        },
        created() {
            this.getData();
            this.getExecUser();
        },
        computed: {
            link(row){
                return row => {
                    return row.stage_id==4?'refundManagement':'salesManagement'
                }
            }
        },
        methods: {
            isShowAudit(row){
                return row.auth_user==1&&row.work_status==2
                /*if(row.stage_id==4){
                    return true
                }*/
            },
            isShowDispose(row){
                if(row.stage_id==1){
                    return row.work_status==3 && row.deal_user==1
                }else{
                    if(row.work_status==1 || row.work_status==3){
                        return row.deal_user==1
                    }
                }
            },
            isShowRefund(row){
                // row.stage_id==1&&row.work_status==1&&row.deal_user==1
                if(row.stage_id==1&&row.deal_user==1){
                    return row.work_status==1 || row.work_status==3
                }
            },
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params= {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.salesManagementList(params).then(res => {
                    this.loading = false
                    if(res.code == 0){
                        this.tableData = res.data.rows;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            },
            getExecUser(){
                // 审核人
                let params = {
                    cp_id:this.company,
                    work_id:1,
                    node_id: 36,
                }
                API.salesManagementGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleDelete(row, index) {
                this.$confirm('确认删除吗？').then(()=>{
                    API.salesManagementDel({id: row.id}).then(res => {
                        if(res.code == 0){
                            this.tableData.splice(index, 1);
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                })
                
            },
            handleRefund(row){
                this.dialogRefund = true
                this.formRefund.sale_id = row.id
                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            // 保存编辑
            dialogRefundSubmit() {
                let params = {
                    sale_id:'', //销售ID
                    why_refund:'', //退款原因
                    factors_img:'', //图片
                    first_exec_id:'', //审核人
                }
                API.salesManagementSaveRefund(this.formRefund).then(res => {
                    if(res.code == 0){
                        this.dialogRefund = false;
                        this.getData()
                        this.$message.success('保存成功')
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            // 确定删除
            deleteRow(){
                this.tableData.splice(this.idx, 1);
                this.$message.success('删除成功');
                this.delVisible = false;
            },
            handleUploadSuccess_refund_img(res, file, fileList){
                this.formRefund.factors_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleBeforeUpload(){

            },
            handleSelectClient(pageType){
                this.dialogSelectClient = true
                this.selectClientOptions.pageType = pageType
                this.hackResetClient = false
                this.$nextTick(() => {
                    this.hackResetClient = true
                })
            },
            emitClientClose(){
                this.dialogSelectClient = false;
            },
            emitClientSubmit(row){
                this.$router.push({path: '/salesManagement/edit', query: {user_id: row.id,user_type: row.id_card?'1':'2'}})
            }
        }
    }

</script>

<style scoped>
    
</style>


