<template>
    <div>
        <!-- 选择客户 -->
		<el-tabs v-model="tabsActive" type="card" @tab-click="handleTabClick" v-if="this.selectClientOptions.pageType == 'default'">
			<el-tab-pane label="个人" name="people"></el-tab-pane>
			<el-tab-pane label="公司" name="company"></el-tab-pane>
		</el-tabs>
        <!-- <el-form ref="filters" :inline="true" :model="filters">
            <el-form-item label="客户名称" :inline="true">
                <el-input v-model.trim="filters.movieName" placeholder="请输入客户名称"></el-input>
            </el-form-item>
            <el-form-item label="电话" :inline="true">
                <el-input v-model.trim="filters.movieName" placeholder="请输入电话"></el-input>
            </el-form-item>
        </el-form> -->
        <el-table :data="tableData" border height="250" v-if="this.selectClientOptions.pageType == 'people'">
            <el-table-column label="" width="150">
                <template slot-scope="{row,$index}">
                    <el-radio v-model="radioActive":label="$index+1" @change="handleSelectClient(row,$index)"></el-radio>
                </template>
            </el-table-column>
            <el-table-column prop="name" label="客户名称" width="120"></el-table-column>
            <el-table-column prop="mobile" label="电话" width="120"></el-table-column>
            <el-table-column prop="id_card" label="身份证号" width="120"></el-table-column>
            <el-table-column prop="partnership_company" label="担保公司" width="120"></el-table-column>
            <el-table-column prop="personal_owner_name" label="担保人" width="120"></el-table-column>
            <el-table-column prop="is_call" label="是否有销售订单挂靠" width="130"></el-table-column>
        </el-table>

        <el-table :data="tableData" border height="250" v-else>
            <el-table-column label="" width="150">
                <template slot-scope="{row,$index}">
                    <el-radio v-model="radioActive":label="$index+1" @change="handleSelectClient(row,$index)"></el-radio>
                </template>
            </el-table-column>
            <el-table-column prop="company_name" label="客户名称" width="120"></el-table-column>
            <el-table-column prop="contact" label="联系人" width="120"></el-table-column>
            <el-table-column prop="mobile" label="电话" width="120"></el-table-column>
            <el-table-column prop="credit_code" label="统一信用代码" width="120"></el-table-column>
            <el-table-column prop="is_call" label="是否有销售订单挂靠" width="130"></el-table-column>
        </el-table>

        <!-- <div class="pagination">
            <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
            </el-pagination>
        </div> -->

        <div class="ta-r pt10">
            <el-button type="primary" @click="handleGoback" v-if="selectClientOptions.goback == true">返回</el-button>
            <el-button @click="$emit('emitClientClose', false)">取 消</el-button>
            <el-button type="primary" @click="handleSubmit()">{{submitText}}</el-button>
        </div>
    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    export default {
        props:{
            selectClientOptions:{
                type: Object,
                default: {
                    tableConfig: {
                        name: false,
                        val: false
                    },
                    formConfig: {
                        val: false
                    },
                    pageType: 'default',
                    isNextButton: false,
                    goback: false,
                }
            }
        },
        data() {
            return {
                tableData: [],
                tableDataObject: {},
                page: 1,
                total: 1,
                limit: 1,
                radioActive: '',
                tabsActive: 'company',
                submitText: '',
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                }
            }
        },
        created() {
            
        },
        mounted(){
            this.checkPath();
            
        },
        computed: {

        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData(this.tabsActive);
            },
            checkPath() {
                let params = {
                    ...this.filters,
                    page: this.page
                }
                this.selectClientOptions.isNextButton ? this.submitText='下一步' : this.submitText='完 成'
                if(this.selectClientOptions.pageType == 'company'){
                    API.apiSelectClientCompany(params).then(res => {
                        if(res.code == 0){
                            this.tableData = res.data;
                            this.total = res.total;
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                	API.apiSelectClientPeople(params).then(res => {
                        if(res.code == 0){
                            this.tableDataObject = res.data;
                            this.getData('people')
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            },
            getData(tabsActive) {
                if(tabsActive == 'company'){
                    this.tableData = this.tableDataObject.company_info;
                }else{
                	this.tableData = this.tableDataObject.person_info;
                }
            },
            handleTabClick(tab){
            	this.getData(tab.paneName)
            },
            handleSelectClient(row,index){

            },
            handleGoback(){
                this.$emit('emitClientGoback', false)
            },
            handleSubmit(){
                this.$emit('emitClientSubmit', this.tableData[this.radioActive-1])
            }
        }
    }

</script>
